import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';

import { Message } from 'primeng/primeng';
import { SelectItem } from 'primeng/primeng';

import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { ComponentConfigDetailsBean } from './bean/component-details.bean';
import { CreditCcmService } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service';

@Component({
  selector: 'app-product-component-config-details',
  templateUrl: './component-config-details.component.html',
  styleUrls: ['./component-config-details.component.scss'],
  providers: [Commfunc]
})

export class ComponentConfigDetailsComponent implements OnInit, OnChanges {
  @Input() inValue: any;
  @Output() outValue: EventEmitter<any> = new EventEmitter();

  caddBean: ComponentConfigDetailsBean = new ComponentConfigDetailsBean();
  productFeatureCategoryId_calue: string;//接受部件ID
  shuxingmiano: any = "";
  vlaue1: any;//01列表型？02添值型
  attribute_type: any;//属性类型
  vlaue2: any;//属性值类型
  value_type: any;//取值类型
  valueTypeAll:any=[]

  //提示信息
  msgs: Message[] = [];

  valueType_List: any[];

  prdcFeaList: any;
  productFeatureLocalCode: string = '';
  min: string = '';
  def: string = '';
  inc: string = '';
  max: string = '';
  productFeatureId: string = '';

  itemvlaue12: any = []//传值空数组转

  featureType: any;
  valueType: any;
  code: any;
  list: any = {};//获取数据
  valueTypeValue: string = '';
  items: any = [{
    description: '',
    productFeatureLocalCode: ''
  }];

  constructor(public creditCcmService: CreditCcmService,
    public commfunc: Commfunc, ) {
    this.codeValues() //调用方法，获取全部码值
    this.featureType = this.code['FeatureType'];
    this.valueType = this.code['ValueType'];
    this.valueTypeAll=this.code['ValueTypeAll']
  }

  ngOnInit() {
  }
  ngOnChanges() {
    let pram = { productFeatureTypeId: this.inValue }
    this.creditCcmService.productFeatureTypeDetail(pram).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.list = data.result;
        this.valueTypeValue = data.result.productFeatures[0].valueType;
        if (this.valueTypeValue === "01") {
          this.items = data.result.productFeatures
        }
        if (this.valueTypeValue != "01") {
          this.inc = data.result.productFeatures[0].inc;
          this.min = data.result.productFeatures[0].min;
          this.max = data.result.productFeatures[0].max;
          this.def = data.result.productFeatures[0].def;
        }

      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    })
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  goBack() {
    this.outValue.emit(false);
  }
}
