import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';

import { Message } from 'primeng/primeng';

import { ComponentViewBean } from "./../bean/component-view.bean";
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { CreditCcmService } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service';

@Component({
  selector: 'app-product-component-view-details',
  templateUrl: './component-view-details.component.html',
  styleUrls: ['./component-view-details.component.scss']
})

export class ComponentViewDetailsComponent implements OnInit, OnChanges {
  tabMesg: any = tableMessage;//列表无数据
  @Input() inValue: any;

  @Output() outValue: EventEmitter<any> = new EventEmitter();
  componentViewBean: ComponentViewBean = new ComponentViewBean();
  cars: any = [];
  //模态框
  showModel: string;
  display: boolean = false;
  //模态框的标题
  headerTitle: string
  updateValue: any = [];
  //提示信息
  msgs: Message[] = [];

  list: any = [];
  productFeatureCategoryId: string;
  listValue: any = [];
  constructor(public creditCcmService: CreditCcmService ) { }
  //分页
  pageSize: number = 10;
  pageNum: number = 1;
  total: string;

  ngOnInit() {
  }

  ngOnChanges() {
    this.queryEvent()
  }
  // 关闭
  closeClick() {
    this.outValue.emit(false);
  }
  queryEvent() {
    this.list = this.inValue;
    let param = {
      productFeatureCategoryId: this.inValue.productFeatureCategoryId,
      pageSize: this.pageSize,
      pageNum: this.pageNum
    };
    this.creditCcmService.prodFeatureCategoryDetails(param).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.total = data.total;
        data.resultList.forEach(item => {
          if (item.min == null) {
            if (item.def != null) {
              item.featureValue = 3;
            } else {
              item.featureValue = 1;
            }
          }
          if (item.min != null) {
            item.featureValue = 2;
          }
        });
        this.listValue = data.resultList;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    });
  }
  //分页
  paginate(event) {
    //每页显示的条数
    this.pageSize = event.rows;
    //当前页
    let currentPage = event.page + 1;
    this.pageNum = currentPage;
    //调用查询的方法
    this.queryEvent();
    this.pageNum = 1;
  }
  //组件详情
  detailsClick(item) {
    this.display = true;
    this.headerTitle = '详情';
    this.showModel = '3';
    this.updateValue = item.productFeatureTypeId;
  }
  //详情回调
  detailsCall(param) {
    this.display = param;
  }
}
