import { ActivatedRoute, Router } from '@angular/router';
import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

import { SelectItem } from 'primeng/primeng';
import { ConfirmationService } from 'primeng/primeng';//删除
import { StepsModule, MenuItem, Message } from 'primeng/primeng';

import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { ComponentConfigBean } from "./../component-config/bean/component-config.bean";
import { CreditCcmService } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service';

@Component({
    selector: 'app-product-component-modify',
    templateUrl: './component-modify.component.html',
    styleUrls: ['./component-modify.component.scss'],
    providers: [CreditCcmService, ConfirmationService, Commfunc]
})

export class ComponentModifyComponent implements OnInit {
    public userform: FormGroup; //校验
    tabMesg: any = tableMessage;//列表无数据
    // bean
    componentConfigBean: ComponentConfigBean = new ComponentConfigBean();
    //修改的参数
    updateValue: any = [];
    select_1: SelectItem[] = [];//默认下拉
    select_2: SelectItem[] = [];//默认下拉
    vlaue1: any;
    vlaue2: any;//列表型？添值型
    items: MenuItem[];
    //头部标题
    all_title: string = '组件配置';
    ceshi0: any;
    ceshi: any;
    featureType: any;
    code: any;
    //模态框
    showModel: string;
    display: boolean = false;
    //模态框的标题
    headerTitle: string
    cars_2: any;//列表
    cars_2Vluel: any;//列表判断添置型还是列表型

    activeIndex: number = 0;
    //提示信息
    msgs: Message[] = [];
    accept_value: any = []
    //组件ID
    productFeatureCategoryId: any;//组件ID
    //分页
    pageSize: number = 10;
    pageNum: number = 1;
    total: string;

    constructor(
        public commfunc: Commfunc,
        public creditCcmService: CreditCcmService,
        public confirmationService: ConfirmationService,
        public router: Router,
        public routers: ActivatedRoute,
        public fb: FormBuilder, ) {
        this.userform = fb.group({
            'description': new FormControl('', [Validators.pattern(/^\S+$/), Validators.required]),
            parentCategoryId: [''],
        })
        this.codeValues() //调用方法，获取全部码值
        this.featureType = this.code['FeatureType'];
    }

    ngOnInit() {
        this.routers.params.subscribe((data) => {
            this.productFeatureCategoryId = data.productFeatureCategoryId;
            if (data.description == "null") {
                this.componentConfigBean.description = ""
            } else {
                this.componentConfigBean.description = data.description;
            }
            if (data.parentCategoryId == "null") {
                this.componentConfigBean.parentCategoryId = ""
            } else {
                this.componentConfigBean.parentCategoryId = data.parentCategoryId;
            }

            this.accept_value.productFeatureCategoryId = data.productFeatureCategoryId;
        })
        this.queryEventA(this.productFeatureCategoryId);

        this.vlaue1 = 0;
        this.select_2 = [
            { label: '请选择', value: " " },
        ];
        this.items = [{
            label: '修改组件',
            command: (event: any) => {
                this.activeIndex = 0;
            }
        },
        {
            label: '维护属性',
            command: (event: any) => {
                this.activeIndex = 1;
            }
        },];
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    queryEventA(itemV) {
        let param = {
            productFeatureCategoryId: itemV,
            pageSize: this.pageSize,
            pageNum: this.pageNum
        };
        this.creditCcmService.prodFeatureCategoryDetails(param).subscribe(data => {
            if (data.returnCode.code == "success") {
                this.total = data.total;
                data.resultList.forEach(item => {
                    if (item.description != null) {
                        item.featureValue = 1;
                    }
                    if (item.min != null) {
                        item.featureValue = 2;
                    }
                    if (item.def != null && item.min == null) {
                        item.featureValue = 3;
                    }

                });
                this.cars_2 = data.resultList;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
        });
    }

    prev() {
        if (this.activeIndex > 0) {
            this.activeIndex = this.activeIndex - 1;
            this.vlaue1 = this.activeIndex;
            this.vlaue2 = this.ceshi;
        }
    }
    next() {
        if (this.activeIndex == 0) {
            // 验证
            for (const i in this.userform.controls) {
                this.userform.controls[i].markAsDirty();
            }
            //处理是否提交
            for (const i in this.userform.controls) {
                if (!this.userform.controls[i].valid) {
                    return;
                }
            }
            let parm = {
                productFeatureCategoryId: this.productFeatureCategoryId,
                parentCategoryId: this.componentConfigBean.parentCategoryId,
                description: this.componentConfigBean.description,
            }
            //修改组件
            let redo_name = {
                xxId: this.productFeatureCategoryId,
                name: this.componentConfigBean.description,
                type: 'PRODUCT_FEATURE_CATEGORY'
            }
            this.creditCcmService.checkBeforeUpdate(redo_name).subscribe(data => {
                if (data.returnCode.code == "success") {
                    if (data.isOrNotUpdate == "is") {
                        //提交服务
                        this.creditCcmService.updateFeatureCategory(parm).subscribe(data => {
                            if (data.returnCode.code == "success") {
                                this.accept_value = parm
                                this.activeIndex = this.activeIndex + 1;
                                this.vlaue1 = this.activeIndex;
                                this.vlaue2 = this.ceshi;
                            } else {
                                this.msgs = [];
                                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                            }
                        }, error => {
                            this.msgs = [];
                            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
                        })
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: data.retDesc + ",不允许修改" });
                    }
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: "调用验证重名服务错误" });
            })

        }
    }

    confirm() {
        this.router.navigate(['/pages/tzb/custom/credit-ccm/productComponentConfig/product-component-view'])
    }
    //新增组件
    addAttribute(item) {
        this.updateValue = this.accept_value.productFeatureCategoryId;
        this.display = true;
        this.headerTitle = '新增';
        this.showModel = '1';
    }
    //新增组件回掉
    addCall(param) {
        if (param.code == 'cancelModel') {
            this.display = param.display;
        } else {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: param.code });
            this.display = param.display;
        }
        this.queryEventA(this.accept_value.productFeatureCategoryId);
    }
    //修改组件
    modifyAttribute(item) {
        this.display = true;
        this.headerTitle = '修改';
        this.showModel = '2';
        this.updateValue = item.productFeatureTypeId;
    }
    //修改组件回掉
    modifyCall(param) {
        if (param.code == 'cancelModel') {
            this.display = param.display;
        } else {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: param.code });
            this.display = param.display;
        }
        this.queryEventA(this.accept_value.productFeatureCategoryId);
    }
    //组件详情
    detailsClick(item) {
        this.display = true;
        this.headerTitle = '详情';
        this.showModel = '3';
        this.updateValue = item.productFeatureTypeId;
    }
    //详情回掉
    detailsCall(param) {
        this.display = param;
    }
    //删除组件
    deleteClick(item) {
        let param = { productFeatureTypeId: item.productFeatureTypeId, productFeatureCategoryId: this.accept_value.productFeatureCategoryId };
        this.confirmationService.confirm({
            message: '确认要删除吗?',
            accept: () => {
                this.creditCcmService.prdcFeaAndFeaTypeDel(param).subscribe(data => {
                    if (data.returnCode.code == "success") {
                        this.msgs = [];
                        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                        this.queryEventA(this.accept_value.productFeatureCategoryId);
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: "删除失败" });
                })
            }
        });
    }
    //分页
    paginate(event) {
        //每页显示的条数
        this.pageSize = event.rows;
        //当前页
        let currentPage = event.page + 1;
        this.pageNum = currentPage;
        //调用查询的方法
        this.queryEventA(this.accept_value.productFeatureCategoryId);
        this.pageNum = 1;
    }
}





