import { ActivatedRoute, Router } from '@angular/router';
import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';

import { SelectItem } from 'primeng/primeng';
import { ConfirmationService } from 'primeng/primeng';//删除
import { StepsModule, MenuItem, Message } from 'primeng/primeng';

import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { ComponentConfigBean } from "./bean/component-config.bean";
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { CreditCcmService } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service';

@Component({
    selector: 'app-product-component-config',
    templateUrl: './component-config.component.html',
    styleUrls: ['./component-config.component.scss'],
    providers: [CreditCcmService, ConfirmationService,Commfunc]
})
/**
 * 组件新增
 */
export class ComponentConfigComponent implements OnInit {
    public tabMesg: any = tableMessage;//列表无数据
    public userform: FormGroup; //校验
    // bean
    public componentConfigBean: ComponentConfigBean = new ComponentConfigBean();
    //修改的参数
    public updateValue: any = [];
    select_1: SelectItem[] = [];//默认下拉
    select_2: SelectItem[] = [];//默认下拉
    public vlaue1: any;
    public vlaue2: any;//列表型？添值型
    items: MenuItem[];
    //头部标题
    public all_title: string = '组件配置';
    public ceshi0: any;
    public ceshi: any;
    public featureType: any;
    code: any;
    //模态框
    public showModel: string;
    display: boolean = false;
    //模态框的标题
    public headerTitle: string
    public cars_2: any;//列表
    public cars_2Vluel: any;//列表判断添置型还是列表型

    activeIndex: number = 0;
    //提示信息
    public msgs: Message[] = [];
    public accept_value: any = [];
    //分页
    pageSize: number = 10;
    pageNum: number = 1;
    total: string;

    constructor(public creditCcmService: CreditCcmService,
        public confirmationService: ConfirmationService,
        public router: Router,
        public fb: FormBuilder,
        public commfunc: Commfunc,  ) {
        this.userform = fb.group({
            'description': new FormControl('', [Validators.pattern(/^\S+$/), Validators.required]),
            parentCategoryId: [''],
        });
        this.codeValues() //调用方法，获取全部码值
        this.featureType = this.code['FeatureType'];
    }

    ngOnInit() {
        this.vlaue1 = 0;
        this.select_2 = [
            { label: '请选择', value: " " },
        ];
        this.items = [{
            label: '新建组件',
            command: (event: any) => {
                this.activeIndex = 0;
            }
        },
        {
            label: '维护属性',
            command: (event: any) => {
                this.activeIndex = 1;
            }
        },];
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    prev() {
        if (this.activeIndex > 0) {
            this.activeIndex = this.activeIndex - 1;
            this.vlaue1 = this.activeIndex;
            this.vlaue2 = this.ceshi;
        }
    }
    next() {
        if (this.activeIndex == 0) {
            // 验证
            for (const i in this.userform.controls) {
                this.userform.controls[i].markAsDirty();
            }
            //处理是否提交
            for (const i in this.userform.controls) {
                if (!this.userform.controls[i].valid) {
                    return;
                }
            }
            //新增组件
            let redo_name = {
                name: this.componentConfigBean.description,
                type: 'PRODUCT_FEATURE_CATEGORY'
            }
            this.creditCcmService.queryNameIsNotExist(redo_name).subscribe(data => {
                if (data.returnCode.code == "success") {
                    if (data.isOrNot == "is") {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: "产品组件名称重复" });
                    } else if (data.isOrNot == "not") {
                        //提交服务
                        this.creditCcmService.addFeatureCategory(this.componentConfigBean).subscribe(data => {
                            if (data.returnCode.code == "success") {
                                this.accept_value = data;
                                this.activeIndex = this.activeIndex + 1;
                                this.vlaue1 = this.activeIndex;
                                this.vlaue2 = this.ceshi;
                                this.msgs = [];
                                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                            } else {
                                this.msgs = [];
                                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                            }
                        }, error => {
                            this.msgs = [];
                            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
                        })
                    }
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: "调用验证重名服务错误" });
            })
        }
    }

    confirm() {
        this.router.navigate(['/pages/tzb/custom/credit-ccm/productComponentConfig/product-component-view'])
    }
    //查询
    attr_query() {
        let param = {
            productFeatureCategoryId: this.accept_value.productFeatureCategoryId,
            pageSize: this.pageSize,
            pageNum: this.pageNum
        };
        this.creditCcmService.prodFeatureCategoryDetails(param).subscribe(data => {
            if (data.returnCode.code == "success") {
                this.total = data.total;
                data.resultList.forEach(item => {
                    if (item.min == null) {
                        if (item.def != null) {
                            item.featureValue = 3;
                        } else {
                            item.featureValue = 1;
                        }
                    }
                    if (item.min != null) {
                        item.featureValue = 2;
                    }
                });
                this.cars_2 = data.resultList;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
        })
    }
    //新增组件
    addAttribute(item) {
        this.updateValue = this.accept_value.productFeatureCategoryId;
        this.display = true;
        this.headerTitle = '新增';
        this.showModel = '1';
    }
    //新增组件回掉
    addCall(param) {
        if (param.code == 'cancelModel') {
            this.display = param.display;
        } else {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: param.code });
            this.display = param.display;
        }
        this.attr_query();
    }
    //修改组件
    modifyAttribute(item) {
        this.display = true;
        this.headerTitle = '修改';
        this.showModel = '2';
        this.updateValue = item.productFeatureTypeId;
    }
    //修改组件回掉
    modifyCall(param) {
        if (param.code == 'cancelModel') {
            this.display = param.display;
        } else {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: param.code });
            this.display = param.display;
        }
        this.attr_query();
    }
    //组件详情
    detailsClick(item) {
        this.display = true;
        this.headerTitle = '详情';
        this.showModel = '3';
        this.updateValue = item.productFeatureTypeId;
    }
    //详情回掉
    detailsCall(param) {
        this.display = param;
    }
    //删除组件
    deleteClick(item) {
        let param = { productFeatureTypeId: item.productFeatureTypeId, productFeatureCategoryId: this.accept_value.productFeatureCategoryId };
        this.confirmationService.confirm({
            message: '确认要删除吗?',
            accept: () => {
                this.creditCcmService.prdcFeaAndFeaTypeDel(param).subscribe(data => {
                    if (data.returnCode.code == "success") {
                        this.msgs = [];
                        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                        this.attr_query();
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: "删除失败" });
                })
            }
        });
    }
    //分页
    paginate(event) {
        //每页显示的条数
        this.pageSize = event.rows;
        //当前页
        let currentPage = event.page + 1;
        this.pageNum = currentPage;
        //调用查询的方法
        this.attr_query();
        this.pageNum = 1;
    }
}





