import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';

import { Message } from 'primeng/primeng';
import { SelectItem } from 'primeng/primeng';

import { ComponentConfigADDBean } from "./bean/config-add.bean";
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { specialSymbol } from 'app/@uisftech/common/service/validator';
import { CreditCcmService } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service';

@Component({
  selector: 'app-product-component-config-add',
  templateUrl: './component-config-add.component.html',
  styleUrls: ['./component-config-add.component.scss'],
  providers: [Commfunc]
})

/**
 * 属性新增
 */
export class ComponentConfigAddComponent implements OnInit {

  @Input() inValue: any;

  @Output() outValue: EventEmitter<any> = new EventEmitter();

  caddBean: ComponentConfigADDBean = new ComponentConfigADDBean();
  userform: FormGroup; //校验
  userformL: FormGroup; //校验取值类型
  userform02: FormGroup; //校验属性值类型-数值型
  userform03: FormGroup; //校验属性值类型-金额型
  userform04: FormGroup; //校验属性值类型-日期型
  userform05: FormGroup; //校验属性值类型-文本型
  specialSymbol = specialSymbol;
  userform06: FormGroup; //校验属性值类型-数值后确定型

  //时间刷新变量
  showFlag: Boolean = true;
  vlaue1: any;//01列表型？02添值型
  attribute_type: any;//属性类型
  vlaue2: any;//属性值类型
  value_type: any;//取值类型

  shuxingmiano: any = "";
  productFeatureTypeId:any;

  items: any = [{
    description: '',
    productFeatureLocalCode: ''
  }];
  //提示信息
  msgs: Message[] = [];
  featureType: any;
  valueType: any;
  code: any;
  valueType_List: any[];

  prdcFeaList: any;

  productFeatureLocalCode: string = '';//添值型本地编码
  min: string;
  def: string;
  inc: string;
  max: string;

  itemvlaue12: any = []//传值空数组转
  //日期
  ch: any = CALENDAR_CN;//讲英文转化为中文
  transferedDateFrom: Date = new Date();
  transferedDateTo: Date = new Date();
  transferedDateDef: Date = new Date();
  
  constructor(public creditCcmService: CreditCcmService,
    public fb: FormBuilder,
    public commfunc: Commfunc, ) {
    this.transferedDateFrom = null;
    this.transferedDateTo = null;
    this.transferedDateDef = null;
    this.userform = fb.group({
      'description': new FormControl('', [Validators.pattern(/^\S+$/), Validators.required]),
      attribute_type: ['', Validators.required],
      shuxingmiano: [''],
      'productFeatureTypeId':new FormControl('',[Validators.pattern(/^PCA\d{7}$/),Validators.required])
    });
    this.userformL = fb.group({
      value_type: ['', Validators.required],
    });
    this.userform02 = fb.group({
      'inc': new FormControl('', [Validators.pattern(/^[0-9]{1,20}(\.[0-9]{0,10})?$/), Validators.maxLength(20)]),
      'min': new FormControl('', [Validators.pattern(/^[0-9]{1,20}(\.[0-9]{0,10})?$/), Validators.maxLength(20), Validators.required]),
      'max': new FormControl('', [Validators.pattern(/^[0-9]{1,20}(\.[0-9]{0,10})?$/), Validators.maxLength(20), Validators.required]),
      // 'def': new FormControl('', [Validators.pattern(/^[0-9]{1,20}(\.[0-9]{0,10})?$/), Validators.maxLength(20)]),
      'def': new FormControl(''),
    });
    this.userform03 = fb.group({
      'inc': new FormControl('', [Validators.pattern(/^[0-9]{1,20}(\.[0-9]{0,2})?$/), Validators.maxLength(20)]),
      'min': new FormControl('', [Validators.pattern(/^[0-9]{1,20}(\.[0-9]{0,2})?$/), Validators.maxLength(20), Validators.required]),
      'max': new FormControl('', [Validators.pattern(/^[0-9]{1,20}(\.[0-9]{0,2})?$/), Validators.maxLength(20), Validators.required]),
      // 'def': new FormControl('', [Validators.pattern(/^[0-9]{1,20}(\.[0-9]{0,2})?$/), Validators.maxLength(20)]),
      'def': new FormControl(''),
    });
    this.userform04 = fb.group({
      transferedDateFrom: ['', Validators.required],
      transferedDateTo: ['', Validators.required],
      transferedDateDef: [''],
    });
    this.userform05 = fb.group({
      // 'def': new FormControl('', [Validators.required, Validators.maxLength(20), specialSymbol]),
      'def': new FormControl(''),
    });
    this.userform06 = fb.group({
      'min': new FormControl('', [Validators.pattern(/^[0-9]{1,20}(\.[0-9]{0,2})?$/), Validators.maxLength(20), Validators.required]),
      'max': new FormControl('', [Validators.pattern(/^[0-9]{1,20}(\.[0-9]{0,2})?$/), Validators.maxLength(20), Validators.required]),
    });
    this.codeValues() //调用方法，获取全部码值
    this.featureType = this.code['FeatureType'];
    this.valueType = this.code['ValueType'];
  }

  ngOnInit() {

  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  //时间刷新
  refresh() {
    this.transferedDateTo = null;
    this.showFlag = !this.showFlag
  }

  addBtn() {
    this.items.push({
      description: '',
      productFeatureLocalCode: ''
    })
  }
  detelBtn(i) {

    this.items.splice(i, 1)
  }
  choseVlue1() {
    this.vlaue1 = this.attribute_type;
    if (this.vlaue1 == '01') {
      this.valueType_List = [
        { label: '列表型', value: '01' },
      ]
    }
  }
  choseVlue12() {
    this.vlaue2 = this.value_type;
    this.inc = '';
    this.min = '';
    this.max = '';
    this.def = '';
  }
  seveClick() {
    // 验证
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    }

    if (this.vlaue1 == '01') {
      this.itemvlaue12 = [];
      if (this.items.length === 0) {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '选项值至少为一条' });
        return;
      }
      this.items.forEach(item => {
        this.itemvlaue12.push(item)
      });
      this.caddBean.prdcFeaList = this.itemvlaue12;
      this.caddBean.valueType = "01";
    }
    if (this.vlaue1 == '02') {
      // 验证
      for (const i in this.userformL.controls) {
        this.userformL.controls[i].markAsDirty();
      }
      //处理是否提交
      for (const i in this.userformL.controls) {
        if (!this.userformL.controls[i].valid) {
          return;
        }
      }
      if (this.vlaue2 == '02') {
        for (const i in this.userform02.controls) {
          this.userform02.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.userform02.controls) {
          if (!this.userform02.controls[i].valid) {
            return;
          }
        }
        this.prdcFeaList = {
          productFeatureLocalCode: this.productFeatureLocalCode,
          min: this.min,
          def: this.def,
          inc: this.inc,
          max: this.max,
        }
        this.itemvlaue12 = []
        this.itemvlaue12.push(this.prdcFeaList)
        this.caddBean.prdcFeaList = this.itemvlaue12;
      }
      if (this.vlaue2 == '03') {
        for (const i in this.userform03.controls) {
          this.userform03.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.userform03.controls) {
          if (!this.userform03.controls[i].valid) {
            return;
          }
        }
        this.prdcFeaList = {
          productFeatureLocalCode: this.productFeatureLocalCode,
          min: this.min,
          def: this.def,
          inc: this.inc,
          max: this.max,
        }
        this.itemvlaue12 = []
        this.itemvlaue12.push(this.prdcFeaList)
        this.caddBean.prdcFeaList = this.itemvlaue12;
      }
      if (this.vlaue2 == '04') {
        for (const i in this.userform04.controls) {
          this.userform04.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.userform04.controls) {
          if (!this.userform04.controls[i].valid) {
            return;
          }
        }
        this.min = this.transDateNW(this.transferedDateFrom);
        this.max = this.transDateNW(this.transferedDateTo);
        this.def = this.transDateNW(this.transferedDateDef);
        this.prdcFeaList = {
          productFeatureLocalCode: this.productFeatureLocalCode,
          min: this.min,
          def: this.def,
          max: this.max,
        }
        this.itemvlaue12 = []
        this.itemvlaue12.push(this.prdcFeaList)
        this.caddBean.prdcFeaList = this.itemvlaue12;
      }
      if (this.vlaue2 == '05') {
        for (const i in this.userform05.controls) {
          this.userform05.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.userform05.controls) {
          if (!this.userform05.controls[i].valid) {
            return;
          }
        }
        this.prdcFeaList = {
          productFeatureLocalCode: this.productFeatureLocalCode,
          def: this.def,
        }
        this.itemvlaue12 = []
        this.itemvlaue12.push(this.prdcFeaList)
        this.caddBean.prdcFeaList = this.itemvlaue12;
      }
      if (this.vlaue2 == '06') {
        for (const i in this.userform06.controls) {
          this.userform06.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.userform06.controls) {
          if (!this.userform06.controls[i].valid) {
            return;
          }
        }
        this.prdcFeaList = {
          productFeatureLocalCode: this.productFeatureLocalCode,
          min: this.min,
          max: this.max,
        }
        this.itemvlaue12 = []
        this.itemvlaue12.push(this.prdcFeaList)
        this.caddBean.prdcFeaList = this.itemvlaue12;
      }
      this.caddBean.valueType = this.value_type;
    }
    this.caddBean.productFeatureCategoryId = this.inValue
    this.caddBean.featureType = this.attribute_type;
    this.caddBean.productFeatureTypeLocalCode = this.shuxingmiano


    let redo_name = {
      name: this.caddBean.description,
      type: 'PRODUCT_FEATURE_TYPE'
    }
    this.creditCcmService.queryNameIsNotExist(redo_name).subscribe(data => {
      if (data.returnCode.code == "success") {
        if (data.isOrNot == "is") {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: "产品属性名称重复" });
        } else if (data.isOrNot == "not") {
          //提交服务
          this.creditCcmService.prdcFeaAndFeaTypeAdd(this.caddBean).subscribe(data => {
            if (data.returnCode.code == "success") {
              this.caddBean = new ComponentConfigADDBean();
              this.shuxingmiano = "";
              this.items = [{
                description: '',
                productFeatureLocalCode: ''
              }];
              this.productFeatureLocalCode = '';
              this.min = '';
              this.max = '';
              this.def = '';
              this.inc = '';
              this.transferedDateFrom = null;
              this.transferedDateTo = null;
              this.transferedDateDef = null;
              this.msgs = [];
              this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
              let parm = {
                display: false,
                code: data.returnCode.message
              }
              this.outValue.emit(parm);
            } else {
              this.msgs = [];
              this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
          }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
          })

        }
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用验证重名服务错误" });
    })
  }
  goBack() {
    let parm = {
      display: false,
      code: 'cancelModel'
    }
    this.outValue.emit(parm);
  }
  transDateNW(value) {
    if (!value) {
      return ''
    }
    let time = new Date(value);
    let year = value.getFullYear();
    let month = value.getMonth() + 1 + '';
    let data = value.getDate() + '';
    if (month.length < 2) {
      month = '0' + month;
    }
    if (data.length < 2) {
      data = "0" + data
    }
    let newtime = year + month + data;
    return newtime;
  }
}

