import { Component, OnInit } from '@angular/core';

import { Message } from 'primeng/primeng';
import { SelectItem } from 'primeng/primeng';
import { ConfirmationService } from 'primeng/primeng';//删除

import { ComponentViewBean } from "./bean/component-view.bean";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { CreditCcmService } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service';

@Component({
  selector: 'app-product-component-view',
  templateUrl: './component-view.component.html',
  styleUrls: ['./component-view.component.scss'],
  providers: [CreditCcmService, ConfirmationService, Commfunc]
})

export class ComponentViewComponent implements OnInit {
  tabMesg: any = tableMessage;//列表无数据
  //组件的传入参数
  updateValue: any = [];
  // bean
  componentViewBean: ComponentViewBean = new ComponentViewBean();

  select_1: SelectItem[] = [];//默认下拉
  select_line: any = [];//产品线下拉
  select_group: any = [];//产品组下拉
  line_value: string;//产品线下拉值
  group_value: string;//产品组下拉值

  //头部标题
  all_title: string = '信贷产品组件管理';
  //列表
  cars_2: any;
  //总页数
  total: string;
  //每页多少条
  pageSize: string = '10';
  //模态框
  showModel: string;
  headerTitle: string
  display: boolean = false;
  //提示信息
  msgs: Message[] = [];
  //分页控制
  first: number = 0;
  constructor(
    public creditCcmService: CreditCcmService,
    public confirmationService: ConfirmationService,
    public commfunc: Commfunc) {
    this.select_1 = [
      { label: '请选择', value: " " },
    ];
    this.select_line = [
      { label: '请选择', value: " " },
    ];
    this.select_group = [
      { label: '请选择', value: " " },
    ];

  }

  ngOnInit() {
    this.select_line = [
      { label: '请选择', value: " " },
    ];
    this.select_group = [
      { label: '请选择', value: " " },
    ];
    this.componentViewBean.pageSize = 10;
    this.componentViewBean.pageNum = 1;
    this.first = 0;
    this.queryClick();
  }

  // 重置
  resetClick() {
    this.componentViewBean = new ComponentViewBean();
  }

  //查询始终第一页
  querySecle() {
    this.componentViewBean.pageSize = 10;
    this.componentViewBean.pageNum = 1;
    this.first = 0;
    this.queryClick();
  }

  //查询
  queryClick() {
    this.componentViewBean.productCategoryLineId = this.line_value;
    this.componentViewBean.productCategoryGroupId = this.group_value;
    this.creditCcmService.productFeatureCategoryList(this.componentViewBean).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.cars_2 = data.productFeatureCategoryList;
        this.total = data.total;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  // 详情
  detailsClick(item) {
    this.display = true;
    this.headerTitle = '详情';
    this.updateValue = item;
    this.showModel = '1';
  }
  //详情的回调
  detailsCall(param) {
    this.display = param;
    // this.queryClick();
  }
  //删除
  deleteClick(item) {
    this.confirmationService.confirm({
      message: '确认要删除吗?',
      accept: () => {
        //条件
        let param = { productFeatureCategoryId: item.productFeatureCategoryId };
        this.creditCcmService.delFeatureCategory(param).subscribe(data => {
          if (data.returnCode.code == "success") {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            this.first = 0;
            this.queryClick();
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: "删除失败" });
        })
      }
    });
  }
  //分页
  paginate(event) {
    //每页显示的条数
    this.componentViewBean.pageSize = event.rows;
    //当前页
    let currentPage = event.page + 1;
    this.componentViewBean.pageNum = currentPage;
    this.first = (currentPage - 1) * this.componentViewBean.pageSize;
    //调用查询的方法
    this.queryClick();
    this.componentViewBean.pageNum = 1;
  }
  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }
}
