import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';

import { Message } from 'primeng/primeng';
import { SelectItem } from 'primeng/primeng';

import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { ComponentConfigADDBean } from "./bean/component-modify.bean";
import { specialSymbol } from 'app/@uisftech/common/service/validator';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { CreditCcmService } from 'app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service';

@Component({
  selector: 'app-product-component-config-modify',
  templateUrl: './component-config-modify.component.html',
  styleUrls: ['./component-config-modify.component.scss'],
  providers: [Commfunc]
})

/**
 * 属性修改
 */
export class ComponentConfigModifyComponent implements OnInit, OnChanges {
  userform: FormGroup; //校验
  userformL: FormGroup; //校验取值类型
  userform02: FormGroup; //校验属性值类型-数值型
  userform03: FormGroup; //校验属性值类型-金额型
  userform04: FormGroup; //校验属性值类型-日期型
  userform05: FormGroup; //校验属性值类型-文本型
  specialSymbol = specialSymbol;
  userform06: FormGroup; //校验属性值类型-数值后确定型

  //日期
  ch: any = CALENDAR_CN;//讲英文转化为中文
  transferedDateFrom: Date = new Date();
  transferedDateTo: Date = new Date();
  transferedDateDef: Date = new Date();

  @Input() inValue: any;

  @Output() outValue: EventEmitter<any> = new EventEmitter();


  // bean
  caddBean: ComponentConfigADDBean = new ComponentConfigADDBean();
  productFeatureCategoryId_calue: string;//接受部件ID
  shuxingmiano: any = "";
  // select_1: SelectItem[] = [];//默认下拉
  vlaue1: any;//01列表型？02添值型
  attribute_type: any;//属性类型
  vlaue2: any;//属性值类型
  value_type: any;//取值类型

  items: any = [{
    description: '',
    productFeatureLocalCode: ''
  }];
  //提示信息
  msgs: Message[] = [];
  featureType: any;
  valueType: any;
  code: any;
  valueType_List: any[];

  prdcFeaList: any;
  productFeatureLocalCode: string = '';
  min: string = '';
  def: string = '';
  inc: string = '';
  max: string = '';
  productFeatureId: string = '';

  itemvlaue12: any = []//传值空数组转

  constructor(
    public creditCcmService: CreditCcmService,
    public commfunc: Commfunc,
    public fb: FormBuilder, ) {
    this.transferedDateFrom = null;
    this.transferedDateTo = null;
    this.transferedDateDef = null;
    this.userform = fb.group({
      'description': new FormControl('', [Validators.pattern(/^\S+$/), Validators.required]),
      attribute_type: ['', Validators.required],
      shuxingmiano: [''],
    });
    this.userformL = fb.group({
      value_type: ['', Validators.required],
    });
    this.userform02 = fb.group({
      'inc': new FormControl('', [Validators.pattern(/^[0-9]{1,20}(\.[0-9]{0,10})?$/), Validators.maxLength(20)]),
      'min': new FormControl('', [Validators.pattern(/^[0-9]{1,20}(\.[0-9]{0,10})?$/), Validators.maxLength(20), Validators.required]),
      'max': new FormControl('', [Validators.pattern(/^[0-9]{1,20}(\.[0-9]{0,10})?$/), Validators.maxLength(20), Validators.required]),
      // 'def': new FormControl('', [Validators.pattern(/^[0-9]{1,20}(\.[0-9]{0,10})?$/), Validators.maxLength(20)]),
      'def': new FormControl(''),
    });
    this.userform03 = fb.group({
      'inc': new FormControl('', [Validators.pattern(/^[0-9]{1,20}(\.[0-9]{0,2})?$/), Validators.maxLength(20)]),
      'min': new FormControl('', [Validators.pattern(/^[0-9]{1,20}(\.[0-9]{0,2})?$/), Validators.maxLength(20), Validators.required]),
      'max': new FormControl('', [Validators.pattern(/^[0-9]{1,20}(\.[0-9]{0,2})?$/), Validators.maxLength(20), Validators.required]),
      // 'def': new FormControl('', [Validators.pattern(/^[0-9]{1,20}(\.[0-9]{0,2})?$/), Validators.maxLength(20)]),
      'def': new FormControl(''),
    });
    this.userform04 = fb.group({
      transferedDateFrom: ['', Validators.required],
      transferedDateTo: ['', Validators.required],
      transferedDateDef: [''],
    });
    this.userform05 = fb.group({
      // 'def': new FormControl('', [Validators.required, Validators.maxLength(20), specialSymbol]),
      'def': new FormControl(''),
    });
    this.userform06 = fb.group({
      'min': new FormControl('', [Validators.pattern(/^[0-9]{1,20}(\.[0-9]{0,2})?$/), Validators.maxLength(20), Validators.required]),
      'max': new FormControl('', [Validators.pattern(/^[0-9]{1,20}(\.[0-9]{0,2})?$/), Validators.maxLength(20), Validators.required]),
    });
    this.codeValues() //调用方法，获取全部码值
    this.featureType = this.code['FeatureType'];
    this.valueType = this.code['ValueType'];
  }

  ngOnInit() {

  }
  ngOnChanges() {
    let pram = { productFeatureTypeId: this.inValue }
    this.creditCcmService.productFeatureTypeDetail(pram).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.productFeatureCategoryId_calue = data.result.productFeatures[0].productFeatureCategoryId;
        this.shuxingmiano = data.result.productFeatureTypeDes;//属性描述
        this.caddBean.productFeatureTypeId = data.result.productFeatureTypeId;// 属性编码
        // // 列表型回显
        if (data.result.featureType == "01") {
          this.valueType_List = [
            { label: '列表型', value: '01' },
          ]
          this.attribute_type = data.result.featureType;//属性类型列表绑定
          this.vlaue1 = this.attribute_type;//列表型
          this.caddBean.description = data.result.description;//属性名
          this.items = data.result.productFeatures//下面的数值
          // data.result.productFeatures.forEach(item => {
          // });
        }
        if (data.result.featureType == "02") {
          this.caddBean.description = data.result.description;//属性名
          this.attribute_type = data.result.featureType;//属性类型绑定
          this.vlaue1 = this.attribute_type;//列表型
          this.value_type = data.result.productFeatures[0].valueType;//取值类型
          this.vlaue2 = this.value_type;//取值类型
          this.productFeatureLocalCode = data.result.productFeatures[0].productFeatureLocalCode;
          if (data.result.featureType == "01") {
            this.items = data.result.productFeatures//下面的数值
          } else {
            this.inc = data.result.productFeatures[0].inc;
            this.min = data.result.productFeatures[0].min;
            this.max = data.result.productFeatures[0].max;
            this.def = data.result.productFeatures[0].def;
            this.productFeatureId = data.result.productFeatures[0].productFeatureId;
            if (data.result.productFeatures[0].valueType == "04") {
              this.inc = data.result.productFeatures[0].inc;
              this.min = data.result.productFeatures[0].min;
              this.max = data.result.productFeatures[0].max;
              this.def = data.result.productFeatures[0].def;
              this.min = this.min.substring(0, 4) + "/" + this.min.substring(4, 6) + "/" + this.min.substring(6, 8);
              this.transferedDateFrom = new Date(this.min);
              this.max = this.max.substring(0, 4) + "/" + this.max.substring(4, 6) + "/" + this.max.substring(6, 8);
              this.transferedDateTo = new Date(this.max);
              if (this.def != '') {
                this.def = this.def.substring(0, 4) + "/" + this.def.substring(4, 6) + "/" + this.def.substring(6, 8);
                this.transferedDateDef = new Date(this.def);
              } else {
                this.transferedDateDef = null;
              }
            } else {
              this.inc = data.result.productFeatures[0].inc;
              this.min = data.result.productFeatures[0].min;
              this.max = data.result.productFeatures[0].max;
              this.def = data.result.productFeatures[0].def;
            }
          }
        }
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    })
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  addBtn() {
    this.items.push({
      description: '',
      productFeatureLocalCode: ''
    })
  }
  detelBtn(i) {
    this.items.splice(i, 1)
  }
  choseVlue1() {
    this.vlaue1 = this.attribute_type;
    if (this.vlaue1 == '01') {
      this.valueType_List = [
        { label: '列表型', value: '01' },
      ]
    }
  }
  choseVlue12() {
    this.vlaue2 = this.value_type;
    this.inc = '';
    this.min = '';
    this.max = '';
    this.def = '';
  }
  goBack() {
    let parm = {
      display: false,
      code: 'cancelModel'
    }
    this.outValue.emit(parm);
  }
  seveClick() {
    // 验证
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    }
    //提交
    if (this.vlaue1 == '01') {
      this.itemvlaue12 = []
      if (this.items.length === 0) {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '选项值至少为一条' });
        return;
      }
      this.items.forEach(item => {
        this.itemvlaue12.push(item)
      });
      this.caddBean.prdcFeaList = this.itemvlaue12;
      this.caddBean.valueType = "01";
    }
    if (this.vlaue1 == '02') {
      // 验证
      for (const i in this.userformL.controls) {
        this.userformL.controls[i].markAsDirty();
      }
      //处理是否提交
      for (const i in this.userformL.controls) {
        if (!this.userformL.controls[i].valid) {
          return;
        }
      }
      if (this.vlaue2 == '02') {
        for (const i in this.userform02.controls) {
          this.userform02.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.userform02.controls) {
          if (!this.userform02.controls[i].valid) {
            return;
          }
        }
        this.prdcFeaList = {
          productFeatureLocalCode: this.productFeatureLocalCode,
          min: this.min,
          def: this.def,
          inc: this.inc,
          max: this.max,
          productFeatureId: this.productFeatureId
        }
        this.itemvlaue12 = []
        this.itemvlaue12.push(this.prdcFeaList)
        this.caddBean.prdcFeaList = this.itemvlaue12;
      }
      if (this.vlaue2 == '03') {
        for (const i in this.userform03.controls) {
          this.userform03.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.userform03.controls) {
          if (!this.userform03.controls[i].valid) {
            return;
          }
        }
        this.prdcFeaList = {
          productFeatureLocalCode: this.productFeatureLocalCode,
          min: this.min,
          def: this.def,
          inc: this.inc,
          max: this.max,
          productFeatureId: this.productFeatureId
        }
        this.itemvlaue12 = []
        this.itemvlaue12.push(this.prdcFeaList)
        this.caddBean.prdcFeaList = this.itemvlaue12;
      }
      if (this.vlaue2 == '04') {
        for (const i in this.userform04.controls) {
          this.userform04.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.userform04.controls) {
          if (!this.userform04.controls[i].valid) {
            return;
          }
        }
        this.min = this.transDateNW(this.transferedDateFrom);
        this.max = this.transDateNW(this.transferedDateTo);
        this.def = this.transDateNW(this.transferedDateDef);
        this.prdcFeaList = {
          productFeatureLocalCode: this.productFeatureLocalCode,
          min: this.min,
          def: this.def,
          max: this.max,
          productFeatureId: this.productFeatureId
        }
        this.itemvlaue12 = []
        this.itemvlaue12.push(this.prdcFeaList)
        this.caddBean.prdcFeaList = this.itemvlaue12;
      }
      if (this.vlaue2 == '05') {
        for (const i in this.userform05.controls) {
          this.userform05.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.userform05.controls) {
          if (!this.userform05.controls[i].valid) {
            return;
          }
        }
        this.prdcFeaList = {
          productFeatureLocalCode: this.productFeatureLocalCode,
          def: this.def,
          productFeatureId: this.productFeatureId
        }
        this.itemvlaue12 = []
        this.itemvlaue12.push(this.prdcFeaList)
        this.caddBean.prdcFeaList = this.itemvlaue12;
      }
      if (this.vlaue2 == '06') {
        for (const i in this.userform06.controls) {
          this.userform06.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.userform06.controls) {
          if (!this.userform06.controls[i].valid) {
            return;
          }
        }
        this.prdcFeaList = {
          productFeatureLocalCode: this.productFeatureLocalCode,
          min: this.min,
          max: this.max,
          productFeatureId: this.productFeatureId
        }
        this.itemvlaue12 = []
        this.itemvlaue12.push(this.prdcFeaList)
        this.caddBean.prdcFeaList = this.itemvlaue12;
      }
      this.caddBean.valueType = this.value_type;
    }
    this.caddBean.productFeatureCategoryId = this.inValue;
    this.caddBean.productFeatureCategoryId = this.productFeatureCategoryId_calue;//部件ID
    this.caddBean.productFeatureTypeLocalCode = '';//没定义的属性本地编码
    this.caddBean.productFeatureTypeId = this.inValue;
    this.caddBean.featureType = this.attribute_type;
    this.caddBean.productFeatureTypeLocalCode = this.shuxingmiano//属性描述
    //提交服务
    let redo_name = {
      xxId: this.inValue,
      name: this.caddBean.description,
      type: 'PRODUCT_FEATURE_TYPE'
    }
    this.creditCcmService.checkBeforeUpdate(redo_name).subscribe(data => {
      if (data.returnCode.code == "success") {
        if (data.isOrNotUpdate == "is") {
          //提交服务
          this.creditCcmService.prdcFeaAndFeaTypeUpd(this.caddBean).subscribe(data => {
            if (data.returnCode.code == "success") {
              let parm = {
                display: false,
                code: data.returnCode.message
              }
              this.outValue.emit(parm);
            } else {
              this.msgs = [];
              this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
          }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
          })
        } else {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: data.retDesc + ",不允许修改" });
        }
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用验证重名服务错误" });
    })
  }


  transDateNW(value) {
    if (!value) {
      return ''
    }
    let time = new Date(value);
    let year = value.getFullYear();
    let month = value.getMonth() + 1 + '';
    let data = value.getDate() + '';
    if (month.length < 2) {
      month = '0' + month;
    }
    if (data.length < 2) {
      data = "0" + data
    }
    let newtime = year + month + data;
    return newtime;
  }
}

